
import { defineComponent } from 'vue';

export default defineComponent({
  props: {
    colorName: {
      type: String,
      required: true,
    },
    distance: {
      type: Number,
      required: true,
    },
  },
  computed: {
    roundedDistance(): number {
      return Math.round(this.distance);
    },
  },
});
